// 
const tagsMap = {
  all: '',
  good: '',
  share: '',
  ask: '',
  top: '',
}

// 
export const setTopicTag = ({tab, isGood, isTop}) => {
  if (isTop) {
    return ''
  } else if (isGood) {
    return ''
  } else if (tagsMap[tab]) {
    return tagsMap[tab]
  } else {
    return null
  }
}


